import React ,{Component}from 'react';
import { Card } from 'react-bootstrap';
import Image from 'react-bootstrap/Image'
import axios from 'axios';
class India extends Component{



  constructor(){

    super();
    this.state={
      Data:{}
    }
  }
   
    componentDidMount(){
    
        axios.get("https://corona.lmao.ninja/v2/countries/india").then(res =>{
       
         this.setState({Data:res.data});
         
         
         
      });
      }


    render(){


           return(
<div className="container-fluid pt-5">

<div className="row">
<div className="col-md-3 p-5">
<Card className="bg-warning" style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title style={{ color: '#ffffff' }}>TOTAL</Card.Title>
           <Card.Subtitle className="mb-2 text-muted"></Card.Subtitle>
    <Card.Text>
    
    </Card.Text>
    
    <h1 className="text-white">{this.state.Data.cases}</h1>
    <h6 className="text-black">[Today-{this.state.Data.todayCases}]</h6>
  </Card.Body>
  
</Card>
</div>

<div className="col-md-3 p-5">
<Card className="bg-success" style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title style={{ color: '#ffffff' }}>ACTIVE</Card.Title>
    <Card.Subtitle className="mb-2 text-muted"></Card.Subtitle>
    <Card.Text>
      
    </Card.Text>
    
    <h1 className="text-white">{this.state.Data.active}</h1>
    <h6 className="text-black">[Today-{this.state.Data.active}]</h6>
    
  </Card.Body>
          
</Card>
</div>

<div className="col-md-3 p-5">
<Card className="bg-primary" style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title style={{ color: '#ffffff' }}>RECOVERED</Card.Title>
    <Card.Subtitle className="mb-2 text-muted"></Card.Subtitle>
    <Card.Text>
      
    </Card.Text>
    
    <h1 className="text-white">{this.state.Data.recovered}</h1>
    <h6 className="text-black">[Today-{this.state.Data.todayRecovered}]</h6>
  </Card.Body>
</Card>
</div>

<div className="col-md-3 p-5">
<Card className="bg-danger" style={{ width: '18rem' }}>
  <Card.Body>
    <Card.Title style={{ color: '#ffffff' }}>DEATH</Card.Title>
    <Card.Subtitle className="mb-2 text-muted"></Card.Subtitle>
    <Card.Text>
      
    </Card.Text>
    
    <h1 className="text-white">{this.state.Data.deaths}</h1>
    <h6 className="text-black">[Today-{this.state.Data.todayDeaths}]</h6>
  </Card.Body>
</Card>
</div>


</div>




</div>






           )

           } 


}

export default India;